import request from '@/utils/request.js'
import qs from 'qs'

export const sendSMSApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/sms/send.php
        url: '/v2/sms/send.php',
        method: 'post',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}

export const loginSMSApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/sms/login.php
        url: '/v2/sms/login.php',
        method: 'post',
        data: qs.stringify(params)
    })
    .then(res => res.data)
}

export const QRcodeApi = params => {
    return request({
        // http://kg.zhaodashen.cn/v2/qr/check.php
        url: '/v2/qr/check.php',
        method: 'post',
        data: qs.stringify({state: params})
    })
    .then(res => res.data)
}

export const getAuthsListApi = () => {
    return request({
        // http://kg.zhaodashen.cn/v2/auth/menu.php
        url: '/v2/auth/menu.php',
    })
    .then(res => res.data)
}