<template>
    <div class="loginSMS">
        <el-form :model="form" :rules="rules" ref="login">
            <el-form-item>
                <h1>短信验证登录</h1>
            </el-form-item>
            <el-form-item prop="mobile">
                <el-input placeholder="请输入手机号" v-model="form.mobile"></el-input>
            </el-form-item>
            <el-form-item>
                <el-input placeholder="请输入验证码" v-model="form.code">
                    <el-button slot="append" :disabled="sendStates" @click="getSMSHandler">
                        {{ sendSMS }}
                    </el-button>
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit" class="login-btn">登录</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import {sendSMSApi, loginSMSApi} from '@/api/login.js'
export default {
    data() {
        return {
            sendSMS: '获取验证码',
            sendStates: false,
            form: {
                mobile: '',
                code: ''
            },
            rules: {
                mobile: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    { len: 11, message: '请输入11位手机号', trigger: 'blur' },
                ]
            }
        }
    },
    methods: {
        onSubmit() {
            this.$refs.login.validate(isSuccess => {
                if(isSuccess) {
                    loginSMSApi({
                        mobile: this.form.mobile,
                        code: this.form.code
                    })
                    .then(res => {
                        console.log(res);
                        if(res.meta.state === 200) {
                            this.$message.success(res.meta.msg)
                        }else {
                            this.$message.error(res.meta.msg)
                        }
                    })
                }else {
                    this.$message.error('error');
                }
            })
        },
        getSMSHandler() {
            // 请求接口
            sendSMSApi({
                mobile: this.form.mobile
            })
            .then(res => {
                console.log(res);
            })
            //
            this.sendStates = true
            let countDown = 5
            this.sendSMS = countDown + 's'
            const timeID = setInterval(() => {
                countDown --
                this.sendSMS = countDown + 's'
                if(countDown === 0) {
                    clearInterval(timeID)
                    this.sendStates = false
                    this.sendSMS = '获取验证码'
                }
            }, 1000)
        }
    }
}
</script>
<style scoped>
    .loginSMS {
        width: 100%;
        height: 100%;
        background-color: #2d3a4b;

        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    .loginSMS .el-form {
        width: 300px;
    }
    .loginSMS .el-form .el-form-item h1{
        font-size: 2em;
        color: #fff;
        /* margin-bottom: 20px; */
    }
    .loginSMS .el-form .el-form-item .login-btn {
        width: 100%;
    }
    .loginSMS .el-form .el-form-item .el-input .el-button {
        width: 100px;
    }
</style>