import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Admin from '../views/admin/index.vue'
// import Welcome from '../views/admin/welcome.vue'
// import Users from '../views/users/index.vue'
// import Create from '../views/users/create.vue'
import Login from '../views/login/index.vue'
import LoginSMS from '../views/login/loginSMS.vue'
// import Roles from '../views/roles/index.vue'
// import CreateRoles from '../views/roles/create.vue'
// import Orders from '../views/orders/index.vue'
// import Recycler from '../views/orders/recycler.vue'
// import Auths from '../views/auths/index.vue'
// import Histroy from '../views/admin/history.vue'
// import Total from '../views/orders/total.vue'
import Not from '../views/404/index.vue'
import {getAuthsListApi} from '@/api/login.js'
Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    name: 'adminindex',
    component: Admin,
    children: [
      // { path: '/welcome',alias: '/', component: Welcome, meta: {name: '欢迎页'} },
      // { path: '/history', component: Histroy, meta: {name: '历史页'} },
      // { path: '/users', component: Users, meta: {name: '用户列表'} },
      // { path: '/users/create', component: Create, meta: {name: '用户创建'} },
      // { path: '/roles', component: Roles, meta: {name: '角色列表'} },
      // { path: '/roles/create', component: CreateRoles, meta: {name: '角色创建'} },
      // { path: '/orders', component: Orders, meta: {name: '订单列表'} },
      // { path: '/orders/recycle', component: Recycler, meta: {name: '回收站'} },
      // { path: '/orders/total', component: Total, meta: {name: '订单统计'} },
      // { path: '/auths', component: Auths, meta: {name: '权限列表'} },

    ]
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/login/sms',
    component: LoginSMS
  },
  {
    path: '/404',
    component: Not
  },
  // {
  //   path: '*',
  //   redirect: '/404'
  // }
]

const router = new VueRouter({
  routes
})

router.beforeEach((newData, oldData, next) => {
  if(newData.path == '/login/sms' || newData.path == '/login') {
      next()
  } else {
      let token = localStorage.getItem('token')
      if (token){
          next()
      }else {
          next({path: '/login'})
      }
  }
 })

 getAuthsListApi().then(res =>{                               
  res.data.forEach(level1 => {
      if(level1 && level1.children.length > 0) {
          level1.children.forEach(level2 => {
              router.addRoute('adminindex', {
                  path: level2.url,
                  component: () => import(`@/views${level2.component}`),
                  meta: {
                      name1: level2.auth_pname,
                      name2: level2.auth_name
                  }
              })
          })
      }
  })
})

export default router
