<template>
    <div class="login">
        <el-form :model="form" :rules="rules" ref="login">
            <h2>B2C电商管理系统</h2>
            <el-form-item>
                <el-select v-model="form.question" placeholder="请输入密保问题">
                <el-option label="您其中一位老师的名字" value="您其中一位老师的名字"></el-option>
                <el-option label="one" value="one"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-input v-model="form.answer" placeholder="请输入密保答案">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </el-form-item>
            <el-form-item prop="uname">
                <el-input v-model="form.uname" placeholder="请输入用户名">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </el-form-item>
            <el-form-item prop="pwd">
                <el-input v-model="form.pwd" placeholder="请输入密码">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </el-form-item>
            <el-form-item class="btn">
                <el-button type="primary" icon="el-icon-camera-solid" @click="QRcodeHandler"></el-button>
                <el-button type="primary" icon="el-icon-mobile-phone" @click="$router.push('/login/sms')"></el-button>
                <el-button type="primary" @click="onSubmit" class="login-btn">提交</el-button>
            </el-form-item>
        </el-form>
        <el-dialog
            title="扫码登录京西商城"
            :visible.sync="dialogVisible"
            width="30%">
            <span class="dialog-body">
                <div class="QRcode" ref="qrCodeUrl"></div>
                <!-- <div class="QRcode">
                    <img src="../../assets/imgs/0258.png" alt="">
                </div> -->
            </span>
        </el-dialog>
    </div>
</template>
<script>
import QRCode from 'qrcodejs2'
import {QRcodeApi, getAuthsListApi} from '@/api/login.js'
export default {
    data() {
        return {
            form: {
                question: '',
                answer: '',
                uname: '',
                pwd: ''
            },
            rules: {
                uname: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 2, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' },
                ],
                pwd: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 10, message: '长度在 6 到 10 个字符', trigger: 'blur' },
                ],
            },
            dialogVisible: false,
            QRcode: '',
        }
    },
    methods: {
        onSubmit() {
            this.$refs.login.validate(isSuccess => {
                if(isSuccess) {
                    // 请求接口
                    // http://kg.zhaodashen.cn/v2/users/login.php
                    this.$axios({
                        url: '/api/v2/users/login.php',
                        method: 'post',
                        data: `question=${this.form.question}&answer=${this.form.answer}&uname=${this.form.uname}&pwd=${this.form.pwd}`
                    })
                    .then(res => res.data)
                    .then(res => {
                        if(res.meta.state === 200) {
                            this.$message.success('success')
                            localStorage.setItem('token', res.data.token)
                            localStorage.setItem('uname', res.data.uname)
                            localStorage.setItem('roleName', res.data.roleName)

                                // this.$router.addRoute('adminindex', {
                                //     path: 'welcome',
                                //     component: () => import('../admin/welcome.vue'),
                                // })
                            getAuthsListApi().then(res =>{
                                res.data.forEach(level1 => {
                                    if(level1 && level1.children.length > 0) {
                                        level1.children.forEach(level2 => {
                                            this.$router.addRoute('adminindex', {
                                                path: level2.url,
                                                component: () => import(`@/views${level2.component}`),
                                                meta: {
                                                    name1: level2.auth_pname,
                                                    name2: level2.auth_name
                                                }
                                            })
                                        })
                                    }
                                })
                                localStorage.setItem('menuList', JSON.stringify(res.data))
                                this.$router.push('/welcome')
                            })
                        }else {
                            this.$message.error(res.meta.msg)
                        }
                    })
                }else {
                    this.$message.error('error');
                }
            })
        },
        uuid() {
            var s = [];
            var hexDigits = "0123456789abcdef";
            for (var i = 0; i < 36; i++) {
                s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
            }
            s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
            s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
            s[8] = s[13] = s[18] = s[23] = "-";

            var uuid = s.join("");
            return (new Date).getTime() + "-" + uuid;
        },
        creatQrCode() {
            this.QRcode = this.uuid()
            if(timerId) clearInterval(timerId)
            let timerId = setInterval(() => {
                QRcodeApi(this.QRcode).then(res => {
                    console.log(res);
                })
            }, 2000)

            this.$refs.qrCodeUrl.innerHTML = ''
            const qrcode = new QRCode(this.$refs.qrCodeUrl, {
                // text: 'https://www.baidu.com',
                text: `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxed58e834201d0894&redirect_uri=http://kg.zhaodashen.cn/v2/qr/login.php&response_type=code&scope=snsapi_base&state=${this.QRcode}&connect_redirect=1#wechat_redirect
`,
                width: 200,
                height: 200,
                colorDark: '#000000',
                colorLight: '#ffffff',
                correctLevel: QRCode.CorrectLevel.H
            })
        },
        QRcodeHandler() {
            this.dialogVisible = !this.dialogVisible
            this.$nextTick(() => {
                this.creatQrCode()
            })
        }
    }
}
</script>
<style scoped>
    .login {
        width: 100%;
        height: 100%;
        background: #2d3a4b;
        display: flex;
        justify-content: center;
        align-items: center
    }
    .login .el-form {
        text-align: center;
        width: 300px;
    }
    .login .el-form h2 {
        font-size: 2em;
        color: #fff;
        margin-bottom: 20px;
    }
    .login .el-form .el-select {
        width: 100%;
    }
    .login .el-form .login-btn {
        width: 168px;
    }
    .login .el-dialog {
        text-align: center;
    }
    .el-dialog .QRcode {
        display: flex;
        justify-content: center;
    }
    .el-dialog .QRcode>img {
        width: 70%;
    }
</style>