<script>
import {getAuthsListApi} from '@/api/login.js'
export default {
    data() {
        return {
            uname: '',
            roleName: '',
            isHideMenu: false,
            menuWidth: '200px',
            menuIcon: 'el-icon-s-fold',
            menuList: []
            // breadcrumb: {
            //     level1: '',
            //     level2: ''
            // }
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        },
        menuHandler() {
            this.isHideMenu = !this.isHideMenu
            this.menuWidth == '200px' ? this.menuWidth = '64px' : this.menuWidth = '200px'
            this.menuIcon == 'el-icon-s-fold' ? this.menuIcon='el-icon-s-unfold' : this.menuIcon = 'el-icon-s-fold'
        },
        open() {
            this.$confirm('此操作将退出登录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message.success('退出成功!');
                localStorage.clear()
                this.$router.push('/')
            }).catch(() => {
                this.$message.info('已取消退出');          
            });
        }
    },
    created() {
        this.roleName = localStorage.getItem('roleName')
        this.uname = localStorage.getItem('uname')
        this.menuList = JSON.parse(localStorage.getItem('menuList'))
    },
}
</script>
